                    <div className="small-11 medium-8 columns padding5_top">
                        <p className="letterCapitalcontent">P</p>
                        <h2>Paradoxes</h2>
                        <p className="text-left content-project">La créativité suppose une acceptation des différents paradoxes qui la composent.</p>
                        <h3>Ordre et désordre</h3>
                        <p className="text-left content-project">Barron définit la sensibilité artistique comme une préférence pour l’ordre, les formes élégantes et l’harmonie. Tous les termes précités sont sujets à des débats passionnants (qu’est ce qui définit une forme élégante ?), mais dans le cadre de cet article, nous nous intéresserons seulement à la notion d’ordre car il s’agit d’un paradoxe intéressant du créateur. Par ordre, dans le domaine créatif, il faut entendre la capacité à organiser son propre désordre.</p>
                        <p className="text-left content-project">Un désordre implique une multitude de données toutes plus différentes les unes que les autres. Plus la culture, l’expérience et la curiosité du créateur sont étendues, plus son désordre sera riche et varié. L’objectif étant d’organiser ce désordre pour ensuite l’utiliser et faire des combinaisons inespérées (Paul Valery) entre les éléments. Certains créateurs, incapables d’ordonner leur désordre intérieur perdent en « combinaisons inespérées » et en clarté. Cela nous amène à la capacité de distanciation propre à tous les créatifs. Distanciation face à son désordre et distanciation face à son travail.</p>
                        <h3>Implication et distanciation</h3>
                        <p className="text-left content-project"><i>- Aide-moi donc, bégaya-t-il, on ne peut pas la laisser comme ça.  L’émotion avait gagné Claude et ses yeux se mouillaient eux aussi, dans sa fraternité d’artiste. Il s’empressa mais le sculpteur, après avoir réclamé son aide, voulait être seul à ramasser ces débris, comme s’il eût craint pour eux la brutalité de tout autre. Lentement, il se traînait à genoux, prenait les morceaux un à un, les couchait, les rapprochait sur une planche. Bientôt la figure fut de nouveau entière, pareille à une de ces suicidées d’amour qui se sont fracassées du haut d’un monument et qu’on recolle, comiques et lamentables pour les porter à la morgue. Lui, retombé sur le derrière devant elle, ne la quittait pas du regard, s’oubliait dans une contemplation navrée.</i></p>
                        <p className="text-left content-project">L’écroulement des rêves de Mahoudeau, extrait de <i>L’Oeuvre</i> par Emile Zola</p>
                        <p className="text-left content-project">Bruner met en valeur la passion qui lie l’objet à l’artiste au fur et à mesure que celui-ci prend forme. L'individu est d’abord possesseur de l’objet lorsqu’il le conçoit, puis il devient possédé par sa propre création. Pour pouvoir reprendre une attitude créative et continuer son travail, il est obligé de se distancer, afin de vérifier si le travail en cours correspond à ses attentes initiales. Ce jeu d’implication/distanciation et Possesseur/Possédé va durer tout au long du processus.</p>
                        <img className="articleIllustration" src={lexicrea_img_Illustr_paradoxes} alt="paradoxes de la créativité" />
                        <h3>Complexité et simplification</h3>
                        <p className="text-left content-project">Le créatif jouit d’un goût paradoxal pour la complexité et la simplification. La complexité car il aime se poser des problèmes et les résoudre. Son goût pour l’analyse l’attirera vers la complexité. La simplification est nécessaire pour pouvoir organiser et catégoriser les éléments qui constituent ses ressources. Un exemple : le créatif graphique, qui effectue un tri entre l’essentiel et l’accessoire, et, généralement, s’efforce à quitter de l’image tout ce qui en complique la perception. </p>
                        <h3>Pensée divergente et pensée convergente</h3>
                        <p className="text-left content-project">Guildford (1967) considérait ces modèles comme les plus importants dans la créativité. En quoi consiste-t-il ?</p>
                        <h4>Pensée divergente</h4>
                        <p className="text-left content-project">La pensée divergente peut être définie comme le mode de pensée qui permet aux individus de générer autant de solutions possibles à partir de contraintes relativement faibles. Un exemple : Guilford (1967) présentait un crayon papier à des individus et leur demandait d'imaginer un grand nombre d'utilisation avec ce crayon. Les résultats étaient comptabilisés en fonction du nombre d'usages imaginés (fluency), le nombre de catégories (flexibility), le degré d'originalité de la réponse en comparaison avec celles du groupe (originality) et la précision des détails (elaboration).
                        La pensée divergente tend donc à repousser les frontières et à élargir les champs du possible.
                        </p>
                        <h4>Pensée convergente</h4>
                        <p className="text-left content-project">La pensée convergente peut être définie comme plus contraignante car il s'agit de trouver des points communs à des éléments et de les unifier dans un unique concept. Dans ses expérimentations, Mednick (1962) proposait trois concepts : cheveux, étirer et temps. Les individus devaient alors identifier le concept qui permettrait d'unifier les trois concepts proposés. La réponse est "longueur".
                        Hudson affirme que les personnes qui sont douées en association libre (associations divergentes) seront attirées par les arts, tandis que les personnes plus douées en associations convergentes seront attirées par la science et la technologie (Oxford, 1998).
                        Cette affirmation peut être contrastée car bien que les deux premières phases de la créativité soient "divergentes", les deux dernières sont "convergentes".
                        </p>
                        <h3>Solitude, collaboration et contexte social</h3>
                        <p className="text-left content-project">Si l'appartenance à une communauté créative facilite l'impulsion créative (recherche de statut, compétition); la solitude permet à l'individu de se centrer pour transformer son idée en réalité.</p>
                    </div>
